.App {
 text-align: center;
 max-width: 100vw;
 max-height: 100vh;
}
 
.container {
 display: flex;
 flex-direction: row;
 align-items: center;
 justify-content: center;
 width: 100%;
 height: 100%;
 background-color: #282c34;
 color: white;
 font-family: sans-serif;
}
 
.left {
 display: flex;
 flex-direction: column;
 width: 70%;
 height: 100vh;
 background-color: #282c34;
 color: white;
 font-family: sans-serif;
 justify-content: flex-start;
 padding: 10px;
 align-items: center;
}
 
.right {
 display: flex;
 flex-direction: column;
 width: 30%;
 height: 100vh;
 background-color: #282c34;
 color: white;
 font-family: sans-serif;
 border-left: 2px solid #777;
 padding: 10px;
 overflow-y: scroll;
}

.stopwatchContainer {
  padding: 10px;
  background-color: gray;
  border-radius: 10px;
  margin-bottom: 10px;
 }
  
 .control-buttons {
  padding: 10px;
  background-color: gray;
  border: none;
 }
  
 .main-button-controls {
  display: flex;
  flex-direction: row;
  gap: 10px;
  width: 100%;
  justify-content: center;
 }
  
 .name-label {
  padding: 10px;
  background-color: gray;
  border-radius: 10px;
  margin-bottom: 10px;
  text-align: left;
  border: none;
  outline: none;
  width: 100%;
  color: white;
 }
  
 .main-time-label {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 10px;
 }
  
 .lap-label{
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: left;
  width: 500px;
  margin-top: 20px;
 }
  
 .lap-container{
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  justify-content: center;
  align-items: center;
 }
  
 .time-label{
  font-size: 25px;
  font-weight: bold;
 }